#TimelineBlock {
  margin-top: 30px;
}
#chatBox {
  color: white;
}

.chat {
  background: rgb(64, 64, 65);
  width: 508px;
  height: 260px;
  position: fixed;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: translate(0px, 220px);
      -ms-transform: translate(0px, 220px);
          transform: translate(0px, 220px);
  bottom: 0;
  right: 100px;
}

.chatExpand {
  cursor:pointer;
  text-align: center;
  margin-top: 10px;
}

.chat.show {
  -webkit-transform: translate(0px, 0px);
      -ms-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.exit {
  cursor:pointer;
  position: absolute;
  top: 10px;
  right: 20px;
}



